.gallery-renderer {
  position: absolute;
  width: 100%;
  justify-self: center;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  img {
    object-fit: cover;
    width: 50%;
    height: 80%;
  }
}

@media screen and (max-width: 850px) {
  .gallery-renderer {
    height: 100%;
    width: 100%;
    backdrop-filter: blur(2px);
    gap: 0.4rem;
    img {
      object-fit: cover;
      object-position: center;
      height: 18rem;
      width: 18rem;
    }
  }
}
